export default {
	data() {
		return {
			news : []
		}
	},
	onLoad(option) {
		uni.showLoading({
			title:"加载中......"
		})
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				this.news = res.data;
				uni.hideLoading();
			},
			fail: () => {},
			complete: () => {}
		})
	},
	methods: {
		openinfo(e) {
			console.log(e)
			uni.navigateTo({
				url: '/pages/info/details?newsId='+e.currentTarget.dataset.newsid
			})
		}
	}
}
